@import "_mixins/styles-mixin";                                          // mixins
@import "_parts/settings";                                               // settings

.mcolor-content {
  display: flex;
  align-items: stretch;
  justify-content: flex-start;
  flex-wrap: wrap;
  padding: 54px 0; // tmp
  @media (max-width: 768px) {
    max-width: 640px;
    margin: 0 auto; }
  &__title {
    flex: 0 0 100%;
    max-width: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin-bottom: 34px;
    @media (max-width: 768px) {
      order: -1; }
    @media (max-width: 450px) {
      flex-wrap: wrap;
      align-items: flex-start; }
    .palette-select {
      position: relative;
      @media (max-width: 450px) {
        flex: 0 0 100%;
        max-width: 100%; }
      &__title {
        &.selectable {
          cursor: pointer;
          .palette-name {
            position: relative;
            &:after {
              content: '';
              display: block;
              width: 18px;
              height: 18px;
              background-image: url("data:image/svg+xml,%3Csvg width='15' height='9' viewBox='0 0 15 9' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 1L7.25 7.25L13.5 1' stroke='%23C5212A' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
              background-repeat: no-repeat;
              background-position: center;
              background-size: contain;
              position: absolute;
              top: 6px;
              right: 5px; } } } }
      .palette-name {
        font-weight: 700;
        font-size: 24px;
        line-height: 28px;
        color: #30363D;
        padding-right: 30px;
        @media (max-width: 450px) {
          font-size: 22px;
          line-height: 24px;
          display: inline-block;
          position: relative; }
        @media (max-width: 360px) {
          font-size: 20px; } }
      .palette__list {
        position: absolute;
        left: 0;
        top: calc(100% + 15px);
        z-index: 10;
        background-color: #fff;
        width: auto;
        white-space: nowrap;
        padding: 20px;
        box-shadow: 0px 0px 20px 4px rgba(171, 179, 189, .4);
        &__item {
          display: block;
          margin-top: 15px;
          &:nth-of-type(1) {
            margin-top: 0; } }
        &__link {
          color: #30363D;
          font-size: 18px;
          line-height: 20px;
          transition: color .3s;
          &:hover {
            color: #D82727; } } } }
    .palette-search {
      margin-left: auto;
      @media (max-width: 450px) {
        flex: 0 0 100%;
        max-width: 100%; }
      .search-form {
        overflow: hidden;
        background-color: #fff;
        box-shadow: 0px 0px 4px 1px #BBBBBB;
        border-radius: 50px;
        @media (max-width: 450px) {
          margin-top: 20px; }
        &.active {
          .input-group {
            input {
              max-width: 100%;
              padding: 10px 20px; } } }
        .input-group {
          display: flex;
          align-items: stretch;
          justify-content: flex-end;
          button {
            flex: 0 0 40px;
            width: 40px;
            height: 40px;
            display: flex;
            align-items: center;
            justify-content: center;
            border: none;
            background-color: transparent;
            cursor: pointer; }
          input {
            background-color: transparent;
            flex: 1 1 100%;
            border: none;
            font-size: 14px;
            line-height: 16px;
            color: #30363D;
            padding: 10px 0;
            max-width: 0;
            @media (max-width: 450px) {
              max-width: 100%;
              padding: 10px 20px; }
            @include input-placeholder {
              color: #535C66; } } } } } }
  &__left-side {
    flex: 0 0 640px;
    max-width: 640px;
    position: relative;
    @media (max-width: 1024px) {
      flex: 0 0 550px;
      max-width: 550px; }
    @media (max-width: 768px) {
      flex: 0 0 100%;
      max-width: 100%;
      order: 1;
      margin-top: 40px; }
    .slide-item {
      position: relative;
      .interier {
        position: relative; }
      .interier__container {
        overflow: hidden;
        position: relative; }
      .interier__item {
        position: relative;
        width: 100%;
        height: 100%;
        opacity: 1; }
      .interier__layout {
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        text-align: center; }
      .interier__image {
        height: 100%;
        > img {
          position: relative;
          left: 0;
          transition: left .5s;
          width: 100%; } }
      .interier__vector {
        position: relative;
        left: 0;
        transition: left .5s;
        width: 100%;
        mix-blend-mode: multiply;
        > g {
          transition: all .2s; }
        &.active {
          & > g {
            cursor: pointer;
            &:hover {
              stroke: #4c4c4c !important; } } } }
      .interier__descriptions {
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        pointer-events: none;
        transition: opacity .5s;
        opacity: 0; }
      .interier__descriptions-inner {
        position: relative;
        left: 0;
        transition: left .5s;
        width: 100%;
        height: 100%; }
      .interier__description {
        width: 90px;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        position: absolute;
        padding: 4px;
        align-items: center;
        background-color: rgb(48, 54, 61);
        opacity: .8;
        left: 0;
        transition: left .3s, opacity .2s; }
      .interier__description-color {
        width: 12px;
        height: 12px;
        margin-right: 5px;
        opacity: 1; }
      .interier__description span {
        display: block;
        font-size: 10px;
        font-weight: 500;
        line-height: 12px;
        color: #FFF;
        opacity: 1; } }
    .swiper-navigation {
      padding: 0 10px;
      .swiper-button-prev {
        left: 15px;
        @media (max-width: 450px) {
          svg {
            max-width: 15px; } } }
      .swiper-button-next {
        right: 15px;
        transform: scale(-1,1);
        @media (max-width: 450px) {
          svg {
            max-width: 15px; } } } }
    .swiper-pagination {
      .swiper-pagination-bullet {
        width: 10px;
        height: 10px;
        background-color: #fff;
        margin: 0 5px !important;
        opacity: 1 !important;
        &.swiper-pagination-bullet-active {
          background-color: rgba(48, 54, 61, 0.9); } } }
    .price-description {
      position: absolute;
      width: 100%;
      top: 100%;
      left: 0;
      text-align: right;
      @media (max-width: 768px) {
        position: relative;
        top: 0; }
      @media (max-width: 450px) {
        text-align: center; } }
    .price-info {
      transition: background-color .3s;
      padding: 20px;
      font-size: 18px;
      line-height: 20px;
      text-align: center;
      @media (max-width: 1024px) {
        font-size: 16px;
        padding: 20px 10px; }
      @media (max-width: 450px) {
        padding: 20px; }
      span:last-child {
        font-weight: 700; } }
    .order-link {
      display: inline-block;
      background-color: #D82727;
      color: #fff;
      transition: background-color .3s;
      font-weight: 700;
      font-size: 18px;
      line-height: 20px;
      margin-top: 35px;
      padding: 12px 60px;
      &:hover {
        background-color: #F4333E; } } }
  &__right-side {
    flex: 0 0 calc(100% - 660px);
    max-width: calc(100% - 660px);
    margin-left: 20px;
    @media (max-width: 1024px) {
      flex: 0 0 calc(100% - 570px);
      max-width: calc(100% - 570px); }
    @media (max-width: 768px) {
      flex: 0 0 100%;
      max-width: 100%;
      margin: 0 auto; }
    .tab-type {
      background: #FFF;
      box-shadow: 0px 0px 20px 4px rgba(171, 179, 189, 0.4);
      padding: 21px 0 0;
      height: 100%;
      position: relative;
      @media (max-width: 768px) {
        padding: 21px 0; }
      h3 {
        text-align: center;
        font-weight: 700;
        font-size: 18px;
        line-height: 20px;
        padding: 0 40px; }
      .shade-close {
        position: absolute;
        right: 15px;
        top: 23px;
        opacity: .7;
        @media (max-width: 450px) {
          top: 24px;
          right: 20px; } }
      .group-list {
        display: flex;
        flex-wrap: wrap;
        align-content: flex-start;
        justify-content: flex-start;
        margin: 20px auto 0;
        max-height: 352px;
        overflow-y: scroll;
        max-width: 462px;
        padding: 10px 5px;
        @media (max-width: 1024px) {
          max-height: 290px;
          max-width: 386px; }
        @media (max-width: 960px) {
          max-height: 285px;
          max-width: 326px; }
        @media (max-width: 768px) {
          padding: 10px 6px 10px 6px;
          max-height: 194px;
          max-width: 640px; }
        @media (max-width: 640px) {
          max-width: 588px; }
        @media (max-width: 576px) {
          max-width: 516px; }
        @media (max-width: 450px) {
          margin: 30px 0 0 15px;
          padding: 0 0 10px 0px;
          overflow-x: scroll;
          overflow-y: hidden;
          max-width: 100%;
          flex-direction: column;
          max-height: 100px;
          &::-webkit-scrollbar {
            height: 4px; } }

        &::-webkit-scrollbar {
          width: 4px; }
        &::-webkit-scrollbar-track {
          background: $scrl-bar_track; }
        &::-webkit-scrollbar-thumb {
          background: #FF9EA3; }
        &::-webkit-scrollbar-thumb:hover {
          background: #FF9EA3; } }
      .group-item {
        display: block;
        flex: 0 0 50px;
        width: 50px;
        height: 50px;
        border-radius: 50%;
        margin: 0 7px 7px 7px;
        cursor: pointer;
        position: relative;
        transition: box-shadow .3s;
        @media (max-width: 1024px) {
          margin: 0 6px 6px 6px; }
        @media (max-width: 960px) {
          width: 40px;
          height: 40px;
          flex: 0 0 40px; }
        @media (max-width: 576px) {
          margin: 0 5px 6px 5px; }
        @media (max-width: 450px) {
          margin: 0 10px 8px 0; }
        &:hover {
          box-shadow: 0px 0px 7px 3px;
          @media (max-width: 450px) {
            box-shadow: none; } }
        &:after {
          content: '';
          display: flex;
          align-items: center;
          justify-content: center;
          background-image: url('../images/svg/check_icon.svg');
          background-repeat: no-repeat;
          background-position: center;
          -webkit-background-size: 20px;
          background-size: 20px;
          z-index: 1;
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          opacity: 0; }
        &.active {
          &:after {
            opacity: 1; } } }
      .shade-list {
        display: flex;
        flex-wrap: wrap;
        align-items: flex-start;
        justify-content: flex-start;
        margin: 15px auto 0;
        padding: 10px 5px 0;
        max-height: 256px;
        overflow-y: scroll;
        max-width: 506px;
        @media (max-width: 1024px) {
          max-height: 215px;
          margin-top: 10px; }
        @media (max-width: 960px) {
          max-height: 174px;
          margin-top: 15px;
          max-width: 342px; }
        @media (max-width: 768px) {
          max-height: 215px;
          margin-top: 15px;
          max-width: 588px; }
        @media (max-width: 576px) {
          padding: 10px 5px 10px 5px;
          max-width: 530px;
          max-height: 230px; }
        @media (max-width: 450px) {
          margin: 30px 0 0 10px;
          padding: 0 0 10px 0px;
          overflow-x: scroll;
          overflow-y: hidden;
          max-width: 100%;
          flex-direction: column;
          &::-webkit-scrollbar {
            height: 4px; } }
        &::-webkit-scrollbar {
          width: 4px; }
        &::-webkit-scrollbar-track {
          background: $scrl-bar_track; }
        &::-webkit-scrollbar-thumb {
          background: #FF9EA3; }
        &::-webkit-scrollbar-thumb:hover {
          background: #FF9EA3; } }
      .shade-item {
        display: block;
        flex: 0 0 35px;
        width: 35px;
        height: 35px;
        margin: 0 3px 6px;
        cursor: pointer;
        position: relative;
        transition: box-shadow .3s;
        @media (max-width: 576px) {
          margin: 0 4px 9px 4px; }
        @media (max-width: 450px) {
          width: 40px;
          height: 40px;
          flex: 0 0 40px;
          margin: 0 5px 5px 0; }
        &:hover {
          box-shadow: 0px 0px 7px 3px;
          @media (max-width: 450px) {
            box-shadow: none; } }
        &:after {
          content: attr(data-code);
          display: flex;
          align-items: center;
          justify-content: center;
          z-index: 1;
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          font-size: 10px;
          line-height: 10px;
          color: #30363D;
          opacity: 0;
          text-align: center;
          transition: opacity .3s; }
        &.active {
          &:after {
            opacity: 1; } } }
      .shade-info {
        margin-top: 20px;
        padding: 0 10px 15px 25px;
        color: #3F3F3F;
        font-size: 14px;
        line-height: 16px;
        position: relative;
        @media (max-width: 1024px) {
          margin-top: 5px;
          padding: 0 10px 10px 25px; }
        @media (max-width: 960px) {
          margin-top: 15px; }
        @media (max-width: 768px) {
          padding: 0 36px 10px 33px; }
        @media (max-width: 576px) {
          padding: 0px 20px 10px 33px; }
        @media (max-width: 460px) {
          padding: 0px 10px 10px 25px; }
        &:before {
          content: '*';
          font-size: 20px;
          line-height: 20px;
          color: red;
          position: absolute;
          top: 2px;
          left: 10px;
          @media (max-width: 768px) {
            left: 15px; }
          @media (max-width: 450px) {
            left: 10px; } } } } }
  &__info {
    flex: 0 0 calc(100% - 665px);
    max-width: calc(100% - 665px);
    margin-left: auto;
    margin-top: 35px;
    @media (max-width: 1024px) {
      flex: 0 0 calc(100% - 575px);
      max-width: calc(100% - 575px); }
    @media (max-width: 768px) {
      flex: 0 0 100%;
      max-width: 100%;
      order: 2; }
    h3 {
      font-size: 15px;
      line-height: 17px;
      @media (max-width: 1024px) {
        font-size: 16px;
        line-height: 20px; } }
    ul {
      font-size: 14px;
      line-height: 16px;
      list-style-type: none;
      margin: 16px 0;
      padding: 0;
      li {
        position: relative;
        padding-left: 10px;
        margin-top: 5px;
        &:nth-of-type(1) {
          margin-top: 0; }
        &:before {
          content: '';
          display: block;
          width: 5px;
          height: 5px;
          border-radius: 50%;
          background-color: #C5212A;
          position: absolute;
          top: 5px;
          left: 0; } } } } }
